.navbar-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 40px;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 10;
  background: transparent;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo img {
  padding-top: 10px;
  width: 100px;
}

.nav-items {
  display: flex;
  gap: 30px;
}

.nav-items h3 {
  color: white;
  font-weight: 400;
  font-size: 15px;
  cursor: pointer;
  transition: color 0.3s;
}

.nav-items h3:hover {
  color: #f5760f;
}

.side-nav-items {
  display: flex;
  align-items: center;
  gap: 16px;
}

.side-nav-items span {
  background-color: #f5760f;
  border: 1px solid #f5760f;
  padding: 6px 18px;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 400;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.side-nav-items span:hover {
  background-color: transparent;
  border: 1px solid white;
  padding: 6px 14px;
  border-radius: 10px;
  color: white;
  font-size: 16px;
}

@media (max-width: 768px) {
  .nav-items {
    display: none;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.9);
    padding: 20px;
    width: 100%;
    height: 100vh;
    transition: transform 0.3s ease-in-out;
    z-index: 100;
  }

  .nav-items.active {
    display: flex;
    opacity: 1;
    transform: translateY(0);
    pointer-events: auto;
  }

  .side-nav-items {
    display: flex;
  }

  .side-nav-items span {
    font-size: 14px;
    padding: 4px 10px;
  }
}